.from-container{
    margin: 4rem 6rem;
    color: #2a2a2a;
}
.from-container form{
    padding-top:3rem ;
    display: flex;
    flex-direction: column;
    width: 50%;
    margin: auto;
}
.from-container input{
    height: 3rem;
    padding: 0 1rem;
    margin-bottom:2rem ;
    border-radius: .3rem;
    border: 1px solid #2a2a2a;
}
.from-container textarea{
    height: 3rem;
    padding:  1rem;
    margin-bottom:2rem ;
    border-radius: .3rem;
    border: 1px solid #2a2a2a;
}
.from-container button{
    display: inline;
}
@media screen and (max-width:850px) {
    .from-container{
        margin: 4rem 2rem;
    }
    .from-container form{
        padding-top:2rem ;
        width: 90%;
    }
    
}